import React from 'react'
import './Aboutread.css';
import { Link } from 'react-router-dom';

const Aboutread = () => {
  return (
      <div className='Aboutread'>
          <div className='ready'>
          <h1>Ready to Transform Your Leadership?</h1>
          <p>Join a community of the like-minded individuals committed to growth. Explore our
              resources or connect with us directly </p>
          <Link to={'/RLOGI/Contact'}><button>Connect with us</button></Link>
          </div>
    </div>
  )
}

export default Aboutread