import React, { useState } from 'react';
import './Navbar.css';
import logo from '../Assets/logo2.png';
import search from '../Assets/search.png';
import { Link, useLocation } from 'react-router-dom';

const Navbar = () => {
  const location = useLocation();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <div className='Navbar'>
      <div className='Navbar-left'>
        <div className='nav-img'>
         <Link to={'/RLOGI'}> <img src={logo} alt='' /> </Link>
        </div>
        <div className={`nav-links ${isMenuOpen ? 'active' : ''}`}>

          <Link to={'/RLOGI'}> <p className={location.pathname === '/RLOGI' ? "active-link" : "link"}>Home</p> </Link>
          <Link to={'/RLOGI/Blog'}> <p className={location.pathname === '/RLOGI/Blog' ? "active-link" : "link" && location.pathname === '/RLOGI/Blogdet' ? "active-link" : "link"}>Blog</p> </Link>
          <Link to={'/RLOGI/About'}> <p className={location.pathname === '/RLOGI/About' ? "active-link" : "link" }>About</p> </Link>
          <Link to={'/RLOGI/Contact'}> <p className={location.pathname === '/RLOGI/Contact' ? "active-link" : "link"}>Contact us</p> </Link>
          <Link to={'/RLOGI/Auth'}><p className={ location.pathname === '/RLOGI/Auth' ? "active-link" : "link link2"}>Sign In</p></Link>
        </div>
      </div>
      <div className='Navbar-right'>
        <div className='search'>
          <img className='search-img' src={search} alt='' />
          <input 
            type='text'
            placeholder='Search articles...' 
          />      
        </div>
        <button className='menu-button' onClick={toggleMenu}>
           {isMenuOpen ? '' : ''}
        </button>
      </div>
    </div>
  );
}

export default Navbar;