import React from 'react'
import './Aboutprinc.css';
import tran from '../Assets/tran.png';
import emp from '../Assets/emp.png';
import comm from '../Assets/comm.png'; 

const Aboutprinc = () => {
  return (
      <div className='Aboutprinc'>
          <h1>Our Guiding Principles</h1>
          <div className='cards'>
              <div className='card'>
                  <img src={tran} alt='' />
                  <h3>Innovation</h3>
                  <p>We embrace forward thinking 
                      approaches and continous learning
                      to deliver cutting edge insights
                      and practical tools
                  </p>
              </div>
               <div className='card'>
                  <img className='com' src={comm} alt='' />
                  <h3>Integrity</h3>
                  <p>We operate with honesty, transparency
                      and a strong ethical compass, building trust
                      in all our interactions
                  </p>
              </div>
               <div className='card'>
                  <img className='em' src={emp} alt='' />
                  <h3>Empowerment</h3>
                  <p>We are dedicated to equipping individuals with
                      the knowledge and confidence to lead meaningful
                      and impactful lives.
                  </p>
              </div>
          </div>
    </div>
  )
}

export default Aboutprinc