import React from 'react'
import './About.css';
import Abouthead from '../Components/Abouthead';
import Aboutjourney from '../Components/Aboutjourney';
import Aboutcomm from '../Components/Aboutcomm';
import Aboutprinc from '../Components/Aboutprinc';
import Aboutteam from '../Components/Aboutteam';
import Aboutread from '../Components/Aboutread';

const About = () => {
  return (
      <div className='About'>
      <div className='About-head'>
        <Abouthead />
      </div>
      <div className='About-jour'>
        <Aboutjourney/>
      </div>
      <div className='About-princ'>
        <Aboutprinc />
      </div>
      <div className='About-team'>
        <Aboutteam/>
      </div>
      <div className='About-comm'>
        <Aboutcomm/>
      </div>
      <div className='About-read'>
        <Aboutread/>
      </div>
    </div>
  )
}

export default About