import React from 'react'
import './Abouthead.css';
import sumpic from '../Assets/sumpic.png';

const Abouthead = () => {
  return (
      <div className='Abouthead'>
          <div className='abt-img'>
              <img src={sumpic} alt='' />
              <div className='overlay'></div>
          </div>
          <div className='writing'>
          <h1>Transforming Mindsets, Inspiring Leaders</h1>
          <p>At RLOGI, we empower individuals and organization to unlock their
              full potential through insightful articles, expert guidance and 
              a community dedicated to growth.
          </p>
          <button className='explore'>Explore Our Blog</button>
          </div>
    </div>
  )
}

export default Abouthead