import React from 'react'
import './Auth.css';
import { Link } from 'react-router-dom';



const Auth = () => {
  return (
      <div className='Auth'>
      
          <h2>Sign in</h2>
          <input type='text' placeholder='Enter your Email..' />
          <input type='' placeholder='Phone no'/> 
          <button>Sign in</button>
          <p><Link to={'/RLOGI/Authlog'}><span> Click here to sign up </span> </Link>if you don't have an account</p>
    </div>
  )
}

export default Auth