import React from 'react'
import './Auth.css';
import { Link } from 'react-router-dom';



const Authlog = () => {
  return (
      <div className='Auth'>
      
          <h2>Sign  Up</h2>
          <input className='user' type='text' placeholder='username' />
          <input className='pass' type='' placeholder='password' />
          <input type='text' placeholder='Enter your Email..' />
          <input type='' placeholder='Phone no'/> 
          <button>Sign in</button>
          <p><Link to={'/RLOGI/Authlog'}><span>Click on sign in </span> </Link>if you already have an account</p>
    </div>
  )
}

export default Authlog