import React from 'react'
import './Contacthead.css';
import loc from '../Assets/loc.png';
import call from '../Assets/call.png';
import mes from '../Assets/mes.png';

const Contacthead = () => {
  return (
      <div className='Contacthead'>
          <div className='conta-top'>
          <h1>Lets's Transform Mindsets Together</h1>
          <p>Reach out to us for collaborations, inquiries or to share 
              your journey. We are here to help you grow
          </p>
          </div>
          <div className='details'>
              <div className='item'>
                  <img src={loc} alt='' />
                  <div className='det'>
                      <small>Our Location</small>
                      <p>Nigeria, Africa</p>
                  </div>
              </div>
              <div className='item'>
                  <img src={mes} alt='' />
                  <div className='det'>
                      <small>Email us</small>
                      <p>rlogi@yahoo.com</p>
                  </div>
              </div>
              <div className='item'>
                  <img src={call} alt='' />
                  <div className='det'>
                      <small>Call us</small>
                      <p>+234 808 0846 021, +234 913 1224 871</p>
                  </div>
              </div>
          </div>
    </div>
  )
}

export default Contacthead