import React from 'react'
import './Aboutjourney.css';

const Aboutjourney = () => {
  return (
      <div className='Aboutjourney'>
          <h1>Our Journey of Transformation </h1>
          <div className='cards'>
              <div className='card'>
                  <small>October 2022</small>
                  <h3>Founding Vision</h3>
                  <p>A clear purpose to build and raise selfless, sacrificial, visionary and revolutionary Leaders
                      that would effect transformational change in every aspect of human stratification thereby becoming
                      agents of societal change.
                  </p>
              </div>
              <div className='card'>
                  <small>November 2022</small>
                  <h3>First Seminar</h3>
                  <p>Held our first live seminar featuring four impactful teaching sessions in Niger state Nigeria with many young
                      people in attendance. Many people gave positive reviews about our business enlightenment teaching, mindset renewal teaching,
                      teaching on revolution and teaching on purpose.
                  </p>
              </div>
              <div className='card'>
                  <small>December 2022</small>
                  <h3>Second Seminar for Teenagers Themed "Rise of Global Giants"</h3>
                  <p>Held our second live seminar for teenagers in Kaduna Nigeria reaching teenagers with mind transforming messages. This will 
                      produce our first online audience.
                  </p>
              </div>
              <div className='card'>
                  <small>January 2023</small>
                  <h3>Launch of Online Teaching Sessions Using Whatsapp and Telegram </h3>
                  <p>We began a one hour daily teaching on whatsapp teaching over 100 people who all testified to have experienced a paradigm shift
                      in their approach towards the matters of leadership and revolution. These teachings still continue till this moment.
                  </p>
              </div>
              <div className='card'>
                  <small>November 2023</small>
                  <h3>Marked Our One Year Anniversary</h3>
                  <p>We marked our one year anniversary doing teaching series that lasted throughout the anniversary week and many were enlightened 
                  </p>
              </div>
          </div>
    </div>
  )
}

export default Aboutjourney